@import url(https://fonts.googleapis.com/css?family=Lato:400,700);

$green: #86BB71;
$blue: #94C2ED;
$orange: #E38968;
$gray: #92959E;

*, *:before, *:after {
  box-sizing: border-box;
}

body {
  background: #C5DDEB;
  font: 14px/20px "Lato", Arial, sans-serif;
  padding: 40px 0;
  color: white;
}

.container {
  margin: 0 auto;
  width: 750px;
  background: #444753;
  border-radius: 5px;
}

.people-list {
  width:260px;
  float: left;
  
  .search {
    padding: 20px;
    input::placeholder{
      color: rgb(136, 238, 199);
    }
  }
  
  input {
    border-radius: 3px;
    border: none;
    padding: 14px;
    color: white;
    background: #6A6C75;
    width: 90%;
    font-size: 14px;
  }
  
  .fa-search {
    position: relative;
    left: -25px;
  }
  
  ul {
    padding: 20px;
    height: 770px;
 
    
    
    
    li {
      padding-bottom: 20px;
      list-style-type: none;

      .name {
        &:hover {
          color: orange;
          cursor: pointer;
        }
      }
    }
  }
  
  img {
    float: left;
    height: 50px;
    width: 50px;
    border-radius:50%;
    -moz-border-radius:50%;
    -webkit-border-radius:50%;
  }
  
  .about {
    float: left;
    margin-top: 8px;
  }
  
  .about {
    padding-left: 8px;
  }
  
  .status {
    color: $gray;
  }
  
}

.chat {
  width: 490px;
  float:left;
  background: #F2F5F8;
  border-top-right-radius: 5px;
  border-bottom-right-radius: 5px;
  
  color: #434651;
  
  .chat-header {
    padding: 20px;
    border-bottom: 2px solid white;
 
    img {
      float: left;
      border-radius:50%;
    }
    
    .chat-about {
      float: left;
      padding-left: 10px;
      margin-top: 6px;
    }
    
    .chat-with {
      font-weight: bold;
      font-size: 16px;
    }
    
    .chat-num-messages {
      color: $gray;
    }
    
    .fa-star {
      float: right;
      font-size: 20px;
      margin-top: 12px;
    }
    .check{
      color: rgb(215, 238, 12);
    }
    .uncheck{
      color: rgb(174, 185, 199);
    }
  }
  
  .chat-history {
    padding: 30px 30px 20px;
    border-bottom: 2px solid white;
    overflow: auto;
    height: 575px;
    
    .message-data {
      margin-bottom: 15px;
      img {
        height: 25px;
        width: 25px;
        border-radius: 50%;
      }
    }
    
    ul {
      li{
        list-style-type: none;
      }
    }
    img {
      height: auto;
      width: 220px;
      display: inherit
    }
    .message-data-time {
      color: lighten($gray, 8%);
      padding-left: 6px;
    }
    
    .message {      
      color: white;
      padding: 18px 20px;
      line-height: 26px;
      font-size: 16px;
      border-radius: 7px;
      margin-bottom: 30px;
      width: 80%;
      position: relative;
      overflow-wrap: break-word;
      
      // &:after {
      //   bottom: 100%;
      //   right: 3%;
      //   border: solid transparent;
      //   content: " ";
      //   height: 0;
      //   width: 0;
      //   position: absolute;
      //   pointer-events: none;
      //   border-bottom-color: $blue;
      //   border-width: 10px;
      //   margin-left: -10px;
      // }
    }
    
    .my-message {
      white-space:pre-wrap;
      background: $blue;
    }
    
    .other-message {
      white-space:pre-wrap;
      background: $green;
      
      // &:after {
      //   border-bottom-color: $green;
      //   right: 93%;
      // }
    }
    
  }
  
  .chat-message {
    padding: 30px;
    
    textarea {
      width: 100%;
      border: none;
      padding: 10px 20px;
      font: 14px/22px "Lato", Arial, sans-serif;
      margin-bottom: 10px;
      border-radius: 5px;
      resize: none;
      
    }
    
    .fa-file-o, .fa-file-image-o {
      font-size: 16px;
      color: gray;
      cursor: pointer;
      
    }
    .previewImages{
      
      .Image{
        float: left;
        img {
          height: auto;
          width: 150px;
          display: inherit
        }
      }
      
    }
    button {
      float: right;
      color: rgb(104, 153, 194);
      font-size: 16px;
      text-transform: uppercase;
      border: none;
      cursor: pointer;
      font-weight: bold;
      background: #F2F5F8;
      
      &:hover {
        color: darken($orange, 7%);
      }
    }
  }
}

.online, .offline, .me {
    margin-right: 3px;
    font-size: 10px;
  }
  
.online {
  color: $green;
}
  
.offline {
  color: $gray;
}

.me {
  color: $blue;
}

.align-left {
  text-align: left;
}

.align-right {
  text-align: right;
}

.float-right {
  float: right;
}

.clearfix:after {
	visibility: hidden;
	display: block;
	font-size: 0;
	content: " ";
	clear: both;
	height: 0;
}

.uploadImage{
  float: left;
  input{
    width: 0px;
    height: 0px;
    opacity: 0;
    overflow: hidden;
    position: absolute;
    z-index: 1;
  }
  label
  {
    float: left;
    cursor: pointer;
    color: #8f5a28;

    &:hover {
      color: darken($green, 10%);
    }
  }


}
