@import url(https://fonts.googleapis.com/css?family=Lato:400,700);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

*, *:before, *:after {
  box-sizing: border-box; }

body {
  background: #C5DDEB;
  font: 14px/20px "Lato", Arial, sans-serif;
  padding: 40px 0;
  color: white; }

.container {
  margin: 0 auto;
  width: 750px;
  background: #444753;
  border-radius: 5px; }

.people-list {
  width: 260px;
  float: left; }
  .people-list .search {
    padding: 20px; }
    .people-list .search input::-webkit-input-placeholder {
      color: #88eec7; }
    .people-list .search input::-ms-input-placeholder {
      color: #88eec7; }
    .people-list .search input::placeholder {
      color: #88eec7; }
  .people-list input {
    border-radius: 3px;
    border: none;
    padding: 14px;
    color: white;
    background: #6A6C75;
    width: 90%;
    font-size: 14px; }
  .people-list .fa-search {
    position: relative;
    left: -25px; }
  .people-list ul {
    padding: 20px;
    height: 770px; }
    .people-list ul li {
      padding-bottom: 20px;
      list-style-type: none; }
      .people-list ul li .name:hover {
        color: orange;
        cursor: pointer; }
  .people-list img {
    float: left;
    height: 50px;
    width: 50px;
    border-radius: 50%;
    -moz-border-radius: 50%;
    -webkit-border-radius: 50%; }
  .people-list .about {
    float: left;
    margin-top: 8px; }
  .people-list .about {
    padding-left: 8px; }
  .people-list .status {
    color: #92959E; }

.chat {
  width: 490px;
  float: left;
  background: #F2F5F8;
  border-top-right-radius: 5px;
  border-bottom-right-radius: 5px;
  color: #434651; }
  .chat .chat-header {
    padding: 20px;
    border-bottom: 2px solid white; }
    .chat .chat-header img {
      float: left;
      border-radius: 50%; }
    .chat .chat-header .chat-about {
      float: left;
      padding-left: 10px;
      margin-top: 6px; }
    .chat .chat-header .chat-with {
      font-weight: bold;
      font-size: 16px; }
    .chat .chat-header .chat-num-messages {
      color: #92959E; }
    .chat .chat-header .fa-star {
      float: right;
      font-size: 20px;
      margin-top: 12px; }
    .chat .chat-header .check {
      color: #d7ee0c; }
    .chat .chat-header .uncheck {
      color: #aeb9c7; }
  .chat .chat-history {
    padding: 30px 30px 20px;
    border-bottom: 2px solid white;
    overflow: auto;
    height: 575px; }
    .chat .chat-history .message-data {
      margin-bottom: 15px; }
      .chat .chat-history .message-data img {
        height: 25px;
        width: 25px;
        border-radius: 50%; }
    .chat .chat-history ul li {
      list-style-type: none; }
    .chat .chat-history img {
      height: auto;
      width: 220px;
      display: inherit; }
    .chat .chat-history .message-data-time {
      color: #a8aab1;
      padding-left: 6px; }
    .chat .chat-history .message {
      color: white;
      padding: 18px 20px;
      line-height: 26px;
      font-size: 16px;
      border-radius: 7px;
      margin-bottom: 30px;
      width: 80%;
      position: relative;
      overflow-wrap: break-word; }
    .chat .chat-history .my-message {
      white-space: pre-wrap;
      background: #94C2ED; }
    .chat .chat-history .other-message {
      white-space: pre-wrap;
      background: #86BB71; }
  .chat .chat-message {
    padding: 30px; }
    .chat .chat-message textarea {
      width: 100%;
      border: none;
      padding: 10px 20px;
      font: 14px/22px "Lato", Arial, sans-serif;
      margin-bottom: 10px;
      border-radius: 5px;
      resize: none; }
    .chat .chat-message .fa-file-o, .chat .chat-message .fa-file-image-o {
      font-size: 16px;
      color: gray;
      cursor: pointer; }
    .chat .chat-message .previewImages .Image {
      float: left; }
      .chat .chat-message .previewImages .Image img {
        height: auto;
        width: 150px;
        display: inherit; }
    .chat .chat-message button {
      float: right;
      color: #6899c2;
      font-size: 16px;
      text-transform: uppercase;
      border: none;
      cursor: pointer;
      font-weight: bold;
      background: #F2F5F8; }
      .chat .chat-message button:hover {
        color: #dd714a; }

.online, .offline, .me {
  margin-right: 3px;
  font-size: 10px; }

.online {
  color: #86BB71; }

.offline {
  color: #92959E; }

.me {
  color: #94C2ED; }

.align-left {
  text-align: left; }

.align-right {
  text-align: right; }

.float-right {
  float: right; }

.clearfix:after {
  visibility: hidden;
  display: block;
  font-size: 0;
  content: " ";
  clear: both;
  height: 0; }

.uploadImage {
  float: left; }
  .uploadImage input {
    width: 0px;
    height: 0px;
    opacity: 0;
    overflow: hidden;
    position: absolute;
    z-index: 1; }
  .uploadImage label {
    float: left;
    cursor: pointer;
    color: #8f5a28; }
    .uploadImage label:hover {
      color: #6aa851; }

